
import InputMask from "primevue/inputmask";
import InputText from "primevue/inputtext";

import { required, alphaNum, maxLength } from "@vuelidate/validators";
import { useVuelidate } from "@vuelidate/core";

import { defineComponent, ref, watch } from "vue";

export default defineComponent({
  name: "PaymentInfo",
  emits: ["formValid", "formInvalid"],
  props: ["userData"],
  components: {
    InputMask,
    InputText,
  },
  setup(props, { emit }) {
    const data = ref({
      card: props.userData.card,
      billingName: props.userData.billingName,
    });
    const rules = {
      card: { required },
      billingName: { required },
    };
    const v$ = useVuelidate(rules, data);

    watch(v$, async (newValue, oldValue) => {
      if (newValue.$invalid) {
        emit("formInvalid");
      } else {
        emit("formValid", data.value);
      }
    });

    return {
      v$,
    };
  },
});
