
import { defineComponent, computed } from "vue";
import ProgressBar from "primevue/progressbar";
import Button from "primevue/button";

export default defineComponent({
  name: "ProgressInfo",
  props: ["value"],
  emits: ["goBack"],
  components: {
    ProgressBar,
    Button,
  },
  setup(props) {
    const progress = computed(() => {
      return props.value;
    });
    return {
      progress,
    };
  },
});
